// 表单验证工具文件

/**
 * 验证用户名
 * @param {string} username - 用户名
 * @returns {Object} 验证结果 {valid: boolean, message: string}
 */
export const validateUsername = (username) => {
  if (!username) {
    return { valid: false, message: '用户名不能为空' };
  }
  
  if (username.length < 3) {
    return { valid: false, message: '用户名至少需要3个字符' };
  }
  
  if (username.length > 20) {
    return { valid: false, message: '用户名不能超过20个字符' };
  }
  
  // 只允许字母、数字、下划线和中划线
  const usernameRegex = /^[a-zA-Z0-9_-]+$/;
  if (!usernameRegex.test(username)) {
    return { valid: false, message: '用户名只能包含字母、数字、下划线和中划线' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 验证密码强度
 * @param {string} password - 密码
 * @returns {Object} 验证结果 {valid: boolean, message: string, strength: string}
 */
export const validatePassword = (password) => {
  if (!password) {
    return { valid: false, message: '密码不能为空', strength: 'weak' };
  }
  
  if (password.length < 8) {
    return { valid: false, message: '密码至少需要8个字符', strength: 'weak' };
  }
  
  if (password.length > 128) {
    return { valid: false, message: '密码不能超过128个字符', strength: 'weak' };
  }
  
  // 检查密码强度
  let strength = 'weak';
  let score = 0;
  
  // 包含小写字母
  if (/[a-z]/.test(password)) score++;
  // 包含大写字母
  if (/[A-Z]/.test(password)) score++;
  // 包含数字
  if (/\d/.test(password)) score++;
  // 包含特殊字符
  if (/[!@#$%^&*(),.?":{}|<>]/.test(password)) score++;
  // 长度大于12
  if (password.length >= 12) score++;
  
  if (score >= 4) {
    strength = 'strong';
  } else if (score >= 3) {
    strength = 'medium';
  }
  
  if (score < 3) {
    return { 
      valid: false, 
      message: '密码强度不足，请包含大小写字母、数字和特殊字符', 
      strength 
    };
  }
  
  return { valid: true, message: '', strength };
};

/**
 * 验证邮箱格式
 * @param {string} email - 邮箱地址
 * @returns {Object} 验证结果 {valid: boolean, message: string}
 */
export const validateEmail = (email) => {
  if (!email) {
    return { valid: false, message: '邮箱不能为空' };
  }
  
  const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
  if (!emailRegex.test(email)) {
    return { valid: false, message: '请输入有效的邮箱地址' };
  }
  
  if (email.length > 254) {
    return { valid: false, message: '邮箱地址过长' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 验证确认密码
 * @param {string} password - 原密码
 * @param {string} confirmPassword - 确认密码
 * @returns {Object} 验证结果 {valid: boolean, message: string}
 */
export const validateConfirmPassword = (password, confirmPassword) => {
  if (!confirmPassword) {
    return { valid: false, message: '请确认密码' };
  }
  
  if (password !== confirmPassword) {
    return { valid: false, message: '两次输入的密码不一致' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 验证MFA验证码
 * @param {string} code - 6位数字验证码
 * @returns {Object} 验证结果 {valid: boolean, message: string}
 */
export const validateMfaCode = (code) => {
  if (!code) {
    return { valid: false, message: '验证码不能为空' };
  }
  
  // 6位数字验证码
  const codeRegex = /^\d{6}$/;
  if (!codeRegex.test(code)) {
    return { valid: false, message: '请输入6位数字验证码' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 验证文件名
 * @param {string} filename - 文件名
 * @returns {Object} 验证结果 {valid: boolean, message: string}
 */
export const validateFilename = (filename) => {
  if (!filename) {
    return { valid: false, message: '文件名不能为空' };
  }
  
  if (filename.length > 255) {
    return { valid: false, message: '文件名过长' };
  }
  
  // 禁止的字符
  const forbiddenChars = /[<>:"/\\|?*]/;
  if (forbiddenChars.test(filename)) {
    return { valid: false, message: '文件名包含非法字符' };
  }
  
  // 禁止的文件名
  const forbiddenNames = ['CON', 'PRN', 'AUX', 'NUL', 'COM1', 'COM2', 'COM3', 'COM4', 'COM5', 'COM6', 'COM7', 'COM8', 'COM9', 'LPT1', 'LPT2', 'LPT3', 'LPT4', 'LPT5', 'LPT6', 'LPT7', 'LPT8', 'LPT9'];
  if (forbiddenNames.includes(filename.toUpperCase())) {
    return { valid: false, message: '文件名为系统保留名称' };
  }
  
  return { valid: true, message: '' };
};

/**
 * 通用表单验证
 * @param {Object} formData - 表单数据
 * @param {Object} rules - 验证规则
 * @returns {Object} 验证结果 {valid: boolean, errors: Object}
 */
export const validateForm = (formData, rules) => {
  const errors = {};
  let valid = true;
  
  for (const field in rules) {
    const value = formData[field];
    const fieldRules = rules[field];
    
    for (const rule of fieldRules) {
      const result = rule.validator(value);
      if (!result.valid) {
        errors[field] = result.message;
        valid = false;
        break; // 一个字段只显示第一个错误
      }
    }
  }
  
  return { valid, errors };
};

/**
 * 常用验证规则
 */
export const validationRules = {
  required: (message = '此字段为必填项') => ({
    validator: (value) => {
      if (value === null || value === undefined || value === '') {
        return { valid: false, message };
      }
      return { valid: true, message: '' };
    }
  }),
  
  minLength: (min, message) => ({
    validator: (value) => {
      if (value && value.length < min) {
        return { valid: false, message: message || `至少需要${min}个字符` };
      }
      return { valid: true, message: '' };
    }
  }),
  
  maxLength: (max, message) => ({
    validator: (value) => {
      if (value && value.length > max) {
        return { valid: false, message: message || `不能超过${max}个字符` };
      }
      return { valid: true, message: '' };
    }
  }),
  
  pattern: (regex, message) => ({
    validator: (value) => {
      if (value && !regex.test(value)) {
        return { valid: false, message };
      }
      return { valid: true, message: '' };
    }
  })
};
