<template>
  <div class="slider-verification">
    <div v-if="loading" class="loading-state">
      <div class="loading-spinner"></div>
      <span>正在加载验证...</span>
    </div>
    
    <div v-else-if="verified" class="success-state">
      <i class="fas fa-check-circle"></i>
      <span>验证成功</span>
    </div>
    
    <div v-else class="slider-track" ref="trackRef">
      <div class="slider-background">
        <div class="slider-fill" :style="{ width: fillWidth + 'px' }"></div>
        <div class="slider-text" :class="{ 'success': verified, 'hidden': isDragging || verified }">
          {{ verified ? '验证成功' : '请拖动滑块到指定位置' }}
        </div>
        
        <!-- 目标位置指示器 -->
        <div 
          v-if="targetX > 0 && !verified" 
          class="target-indicator"
          :style="{ left: targetX + 'px' }"
        >
          <div class="target-icon">⭕</div>
        </div>
      </div>
      
      <div 
        class="slider-button" 
        ref="buttonRef"
        :style="{ left: buttonPosition + 'px' }"
        :class="{ 'dragging': isDragging, 'success': verified }"
        @mousedown="handleMouseDown"
        @touchstart="handleTouchStart"
      >
        <i v-if="!verified" class="fas fa-arrow-right"></i>
        <i v-else class="fas fa-check"></i>
      </div>
    </div>
    
    <div v-if="errorMessage" class="error-message">
      {{ errorMessage }}
    </div>
    
    <div class="retry-button" v-if="errorMessage && !loading">
      <button @click="initChallenge">重新验证</button>
    </div>
  </div>
</template>

<script setup>
import { ref, onMounted, defineEmits, defineExpose } from 'vue'


const emit = defineEmits(['verified', 'failed'])

// 响应式数据
const trackRef = ref()
const buttonRef = ref()
const loading = ref(false)
const verified = ref(false)
const isDragging = ref(false)
const errorMessage = ref('')
const buttonPosition = ref(0)
const fillWidth = ref(0)
const robotsJwt = ref('')
const targetX = ref(0) // 目标位置（像素）
const tolerance = ref(20) // 容差（像素）
const challenge = ref(null)

// 初始化验证挑战
async function initChallenge() {
  try {
    loading.value = true
    errorMessage.value = ''
    verified.value = false
    buttonPosition.value = 0
    fillWidth.value = 0
    
    const response = await fetch('/api/auth/login.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: 'action=get_slider'
    });
    const result = await response.json();
    if (result.status === 'ok') {
      robotsJwt.value = result.robots_jwt;
      // 根据滑块轨道宽度计算目标像素位置
      const trackWidth = trackRef.value?.getBoundingClientRect().width || 300;
      // 将百分比转换为像素位置，确保目标在合理范围内
      const minTargetX = trackWidth * 0.2; // 最小20%位置
      const maxTargetX = trackWidth * 0.8; // 最大80%位置
      targetX.value = Math.floor(Math.random() * (maxTargetX - minTargetX)) + minTargetX;
      tolerance.value = 20; // 固定20像素容差
    } else {
      throw new Error('获取验证挑战失败');
    }
    
  } catch (error) {
    errorMessage.value = error.message || '初始化验证失败'
  } finally {
    loading.value = false
  }
}

// 开始拖拽
function startDrag(startX) {
  if (verified.value || loading.value) return
  
  isDragging.value = true
  errorMessage.value = ''
  
  const trackRect = trackRef.value.getBoundingClientRect()
  const buttonWidth = 50
  // 修正：滑块区间为整个轨道宽度（不减按钮宽度），百分比计算与后端一致
  const maxPosition = trackRect.width - buttonWidth
  
  const handleMove = (e) => {
    if (!isDragging.value) return
    
    const currentX = e.type.includes('touch') ? e.touches[0].clientX : e.clientX
    const deltaX = currentX - startX
    let newPosition = Math.max(0, Math.min(deltaX, maxPosition))
    
    buttonPosition.value = newPosition
    fillWidth.value = newPosition + buttonWidth / 2
  }
  
  const handleEnd = async () => {
    if (!isDragging.value) return
    
    isDragging.value = false
    
    try {
      // 获取滑块中心位置（像素）
      const buttonWidth = 50;
      const sliderCenterPx = buttonPosition.value + buttonWidth / 2;
      
      // 添加小幅随机偏移防止自动化攻击
      const randomOffset = Math.floor(Math.random() * 10) - 5; // -5到+5的随机偏移
      const adjustedPosition = Math.round(sliderCenterPx) + randomOffset;
      
      const response = await fetch('/api/auth/login.php', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/x-www-form-urlencoded'
        },
        body: `action=verify_slider&robots_jwt=${encodeURIComponent(robotsJwt.value)}&user_position=${encodeURIComponent(adjustedPosition)}&target_position=${encodeURIComponent(targetX.value)}`
      });
      const result = await response.json();
      if (result.status === 'slider_ok') {
        verified.value = true;
        emit('verified', result.account_jwt);
      } else {
        throw new Error('验证失败，请重试');
      }
    } catch (error) {
      errorMessage.value = error.message || '验证失败，请重试';
      buttonPosition.value = 0;
      fillWidth.value = 0;
    }
  }
  
  // 添加事件监听器
  document.addEventListener('mousemove', handleMove)
  document.addEventListener('mouseup', handleEnd, { once: true })
  document.addEventListener('touchmove', handleMove)
  document.addEventListener('touchend', handleEnd, { once: true })
}

// 鼠标/触摸事件处理
function handleMouseDown(e) {
  if (verified.value || loading.value) return
  e.preventDefault()
  startDrag(e.clientX)
}

function handleTouchStart(e) {
  if (verified.value || loading.value) return
  e.preventDefault()
  const touch = e.touches[0]
  startDrag(touch.clientX)
}

// 暴露方法
defineExpose({
  initChallenge,
  reset: () => {
    verified.value = false
    buttonPosition.value = 0
    fillWidth.value = 0
    errorMessage.value = ''
    robotsJwt.value = ''
    targetX.value = 0
    tolerance.value = 20
    challenge.value = null
  }
})

// 组件挂载时初始化
onMounted(() => {
  initChallenge()
})
</script>

