<template>
  <div class="init-form-wrapper">
    
    <!-- 伪静态检查警告 -->
    <div v-if="showRewriteWarning" class="rewrite-warning">
      <div class="warning-header">
        <i class="fas fa-exclamation-triangle"></i>
        <span>检测到安全风险</span>
      </div>
      <p>系统检测到敏感目录可被直接访问，建议立即配置伪静态规则以保护数据安全：</p>
      <div class="rewrite-rules">
        <div class="rule-section">
          <h4><i class="fab fa-nginx"></i> Nginx 配置</h4>
          <pre><code>location / {
    try_files $uri $uri/ /index.html;
}
location ~ ^/api/(auth|file|share)/data/ {
    deny all;
    return 403;
}</code></pre>
        </div>
        <div class="rule-section">
          <h4><i class="fab fa-apache"></i> Apache 配置 (.htaccess)</h4>
          <pre><code>RewriteEngine On
RewriteCond %{REQUEST_FILENAME} !-f
RewriteCond %{REQUEST_FILENAME} !-d
RewriteRule ^(.*)$ /index.html [L]
RewriteRule ^api/(auth|file|share)/data/ - [F,L]</code></pre>
        </div>
      </div>
      <p class="warning-footer">配置完成后刷新页面重新检测</p>
    </div>
    
    <p class="init-tip">系统尚未初始化，请创建管理员账户</p>
    
    <el-form ref="initFormRef" :model="initForm" :rules="initRules" class="common-form">
      <el-form-item prop="username" :error="errors.username">
        <el-input
          v-model="initForm.username"
          placeholder="管理员用户名（最多50字符）"
          prefix-icon="User"
          size="large"
          @blur="validateField('username')"
          @input="clearError('username')"
          :maxlength="50"
        />
      </el-form-item>
      
      <el-form-item prop="password" :error="errors.password">
        <el-input
          v-model="initForm.password"
          type="password"
          placeholder="管理员密码（8-32位）"
          prefix-icon="Lock"
          size="large"
          show-password
          @blur="validateField('password')"
          @input="clearError('password')"
          :maxlength="32"
        />
      </el-form-item>
      
      <el-form-item prop="confirmPassword" :error="errors.confirmPassword">
        <el-input
          v-model="initForm.confirmPassword"
          type="password"
          placeholder="确认密码"
          prefix-icon="Lock"
          size="large"
          show-password
          @blur="validateField('confirmPassword')"
          @input="clearError('confirmPassword')"
          :maxlength="32"
        />
      </el-form-item>
      
      <!-- 邮箱设置已移除 -->
      
      <!-- 实时验证提示 -->
      <div v-if="hasValidationErrors" class="validation-tips">
        <div v-for="errorInfo in visibleErrors" :key="errorInfo.field" class="error-tip">
          <i class="fas fa-exclamation-triangle"></i>
          <span>{{ errorInfo.message }}</span>
        </div>
      </div>
      
      <el-button
        type="primary"
        size="large"
        :loading="loading"
        :disabled="!isFormValid"
        @click="handleInit"
        class="primary-btn"
      >
        <i class="fas fa-rocket"></i>
        初始化系统
      </el-button>
    </el-form>
  </div>
</template>

<script setup>
import { ref, reactive, computed, defineEmits, onMounted } from 'vue';
import { ElMessage } from 'element-plus';
import { validateUsername, validatePassword } from '../../utils/validator';

// Emits
const emit = defineEmits(['init-success']);

// 响应式数据
const loading = ref(false);
const showRewriteWarning = ref(false);

// 检查伪静态配置
const checkRewriteRules = async () => {
  try {
    const protocol = window.location.protocol;
    const host = window.location.host;
    const testUrl = `${protocol}//${host}/api/auth/data/test.json`;
    
    const response = await fetch(testUrl, {
      method: 'GET',
      cache: 'no-cache'
    });
    
    // 如果能够访问到该文件（状态码200），说明伪静态未配置
    if (response.status === 200) {
      showRewriteWarning.value = true;
    } else {
      showRewriteWarning.value = false;
    }
  } catch (error) {
    // 网络错误或403/404等状态码，说明伪静态可能已配置
    showRewriteWarning.value = false;
  }
};

// 验证错误信息
const errors = reactive({
  username: '',
  password: '',
  confirmPassword: ''
});

// 初始化表单
const initForm = reactive({
  username: '',
  password: '',
  confirmPassword: ''
});

// 计算属性：检查是否有验证错误
const hasValidationErrors = computed(() => {
  return Object.values(errors).some(error => error !== '');
});

// 计算属性：获取可见的错误信息
const visibleErrors = computed(() => {
  return Object.entries(errors)
    .filter(([, message]) => message !== '')
    .map(([field, message]) => ({ field, message }));
});

// 计算属性：检查表单是否有效
const isFormValid = computed(() => {
  const hasRequiredValues = initForm.username.trim() && 
                           initForm.password && 
                           initForm.confirmPassword;
  const noErrors = !hasValidationErrors.value;
  const passwordsMatch = initForm.password === initForm.confirmPassword;
  return hasRequiredValues && noErrors && passwordsMatch;
});

// 验证单个字段
const validateField = (field) => {
  const value = initForm[field];
  let result;
  
  switch (field) {
    case 'username':
      result = validateUsername(value);
      break;
    case 'password':
      result = validatePassword(value);
      break;
    case 'confirmPassword':
      if (!value) {
        result = { valid: false, message: '请确认密码' };
      } else if (value !== initForm.password) {
        result = { valid: false, message: '两次输入的密码不一致' };
      } else {
        result = { valid: true, message: '' };
      }
      break;
    // 邮箱已移除
    default:
      result = { valid: true, message: '' };
  }
  
  errors[field] = result.valid ? '' : result.message;
  
  // 如果确认密码字段有值，同时验证密码一致性
  if (field === 'password' && initForm.confirmPassword) {
    validateField('confirmPassword');
  }
  
  return result.valid;
};

// 清除单个字段错误
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 验证整个表单
const validateForm = () => {
  // 直接使用单个验证函数而不是复杂的规则系统
  let isValid = true;
  
  // 清空所有错误
  Object.keys(errors).forEach(key => {
    errors[key] = '';
  });
  
  // 验证用户名
  const usernameResult = validateUsername(initForm.username);
  if (!usernameResult.valid) {
    errors.username = usernameResult.message;
    isValid = false;
  }
  
  // 验证密码
  const passwordResult = validatePassword(initForm.password);
  if (!passwordResult.valid) {
    errors.password = passwordResult.message;
    isValid = false;
  }
  
  // 邮箱已移除
  
  // 验证确认密码
  if (!initForm.confirmPassword) {
    errors.confirmPassword = '请确认密码';
    isValid = false;
  } else if (initForm.password !== initForm.confirmPassword) {
    errors.confirmPassword = '两次输入的密码不一致';
    isValid = false;
  }
  
  return isValid;
};

// 表单引用
const initFormRef = ref();

// Element Plus 表单验证规则（备用验证）
const initRules = {
  username: [
    { 
      validator: (rule, value, callback) => {
        const result = validateUsername(value);
        if (result.valid) {
          callback();
        } else {
          callback(new Error(result.message));
        }
      }, 
      trigger: 'blur' 
    }
  ],
  password: [
    { 
      validator: (rule, value, callback) => {
        const result = validatePassword(value);
        if (result.valid) {
          callback();
        } else {
          callback(new Error(result.message));
        }
      }, 
      trigger: 'blur' 
    }
  ],
  confirmPassword: [
    {
      validator: (rule, value, callback) => {
        if (!value) {
          callback(new Error('请确认密码'));
        } else if (value !== initForm.password) {
          callback(new Error('两次输入的密码不一致'));
        } else {
          callback();
        }
      },
      trigger: 'blur'
    }
  ],
  // 邮箱已移除
};

// 处理系统初始化
const handleInit = async () => {
  if (!validateForm()) {
    ElMessage.error('请检查输入内容');
    return;
  }
  
  const valid = await initFormRef.value.validate().catch(() => false);
  if (!valid) return;
  
  loading.value = true;
  
  try {
    const response = await fetch('/api/auth/login.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: `username=${encodeURIComponent(initForm.username.trim())}&password=${encodeURIComponent(initForm.password)}`
    });
    const result = await response.json();
    if (result.status === 'ok' || result.success) {
      ElMessage.success('系统初始化成功！');
      // 清空表单
      Object.assign(initForm, {
        username: '',
        password: '',
        confirmPassword: '',
        email: ''
      });
      // 清空错误信息
      Object.keys(errors).forEach(key => {
        errors[key] = '';
      });
      emit('init-success');
    } else {
      ElMessage.error(result.message || '初始化失败，可能是密码强度不够');
    }
  } catch (error) {
    ElMessage.error('初始化请求失败，请检查网络连接');
  } finally {
    loading.value = false;
  }
};

// 组件挂载时检查伪静态配置
onMounted(() => {
  checkRewriteRules();
});
</script>

