<template>
  <div class="form-wrapper">
    <div class="form-title">
      <i class="fas fa-sign-in-alt"></i>
      <span>用户登录</span>
    </div>
    
    <el-form ref="loginFormRef" :model="loginForm" :rules="loginRules" class="common-form">
      <el-form-item prop="username" :error="errors.username">
        <el-input
          v-model="loginForm.username"
          placeholder="用户名"
          prefix-icon="User"
          size="large"
          @blur="validateField('username')"
          @input="clearError('username')"
          :maxlength="50"
        />
      </el-form-item>
      
      <el-form-item prop="password" :error="errors.password">
        <el-input
          v-model="loginForm.password"
          type="password"
          placeholder="密码"
          prefix-icon="Lock"
          size="large"
          show-password
          @keyup.enter="handleLogin"
          @blur="validateField('password')"
          @input="clearError('password')"
          :maxlength="32"
        />
      </el-form-item>
      
      <!-- 实时验证提示 -->
      <div v-if="hasValidationErrors" class="validation-tips">
        <div v-for="errorInfo in visibleErrors" :key="errorInfo.field" class="error-tip">
          <i class="fas fa-exclamation-triangle"></i>
          <span>{{ errorInfo.message }}</span>
        </div>
      </div>
      
      <!-- 登录按钮 -->
      <el-button
        type="primary"
        size="large"
        :loading="loading"
        :disabled="!isFormValid"
        @click="handleLogin"
        class="primary-btn"
      >
        <i class="fas fa-sign-in-alt"></i>
        登录
      </el-button>
    </el-form>
    
    <!-- 安全提示 -->
    <div class="warning-tip-simple">
      <i class="fas fa-exclamation-triangle"></i>
      <span>多次验证失败会被暂时封禁IP地址</span>
    </div>
  </div>
</template>

<script setup>
import { ref, reactive, computed, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { saveLoginTokens } from '../../utils/auth';
import { validateUsername, validatePassword } from '../../utils/validator';

// Props
const props = defineProps({
  accountToken: {
    type: String,
    required: true
  }
});

// Emits
const emit = defineEmits(['login-success', 'need-mfa']);

// 响应式数据
const loading = ref(false);

// 验证错误信息
const errors = reactive({
  username: '',
  password: ''
});

// 登录表单
const loginForm = reactive({
  username: '',
  password: ''
});

// 表单引用
const loginFormRef = ref();

// 计算属性：检查是否有验证错误
const hasValidationErrors = computed(() => {
  return Object.values(errors).some(error => error !== '');
});

// 计算属性：获取可见的错误信息
const visibleErrors = computed(() => {
  return Object.entries(errors)
    .filter(([, message]) => message !== '')
    .map(([field, message]) => ({ field, message }));
});

// 计算属性：检查表单是否有效
const isFormValid = computed(() => {
  const hasValues = loginForm.username.trim() && loginForm.password;
  const noErrors = !hasValidationErrors.value;
  const hasToken = props.accountToken && props.accountToken.trim().length > 0;
  return hasValues && noErrors && hasToken;
});

// 验证单个字段
const validateField = (field) => {
  const value = loginForm[field];
  let result;
  
  switch (field) {
    case 'username':
      result = validateUsername(value);
      break;
    case 'password':
      result = validatePassword(value);
      break;
    default:
      result = { valid: true, message: '' };
  }
  
  errors[field] = result.valid ? '' : result.message;
  return result.valid;
};

// 清除单个字段错误
const clearError = (field) => {
  if (errors[field]) {
    errors[field] = '';
  }
};

// 验证整个表单
const validateForm = () => {
  // 清空所有错误
  Object.keys(errors).forEach(key => {
    errors[key] = '';
  });
  
  let isValid = true;
  
  // 验证用户名
  const usernameResult = validateUsername(loginForm.username);
  if (!usernameResult.valid) {
    errors.username = usernameResult.message;
    isValid = false;
  }
  
  // 验证密码
  const passwordResult = validatePassword(loginForm.password);
  if (!passwordResult.valid) {
    errors.password = passwordResult.message;
    isValid = false;
  }
  
  return isValid;
};

// 登录表单验证规则（Element Plus的备用验证）
const loginRules = {
  username: [
    { 
      validator: (rule, value, callback) => {
        const result = validateUsername(value);
        if (result.valid) {
          callback();
        } else {
          callback(new Error(result.message));
        }
      }, 
      trigger: 'blur' 
    }
  ],
  password: [
    { 
      validator: (rule, value, callback) => {
        const result = validatePassword(value);
        if (result.valid) {
          callback();
        } else {
          callback(new Error(result.message));
        }
      }, 
      trigger: 'blur' 
    }
  ]
};

// 处理登录
const handleLogin = async () => {
  if (!validateForm()) {
    ElMessage.error('请检查输入内容');
    return;
  }
  
  if (!props.accountToken || !props.accountToken.trim()) {
    ElMessage.error('请先完成人机验证');
    return;
  }
  
  const valid = await loginFormRef.value.validate().catch(() => false);
  if (!valid) return;
  
  loading.value = true;
  
  try {
    const response = await fetch('/api/auth/login.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: `action=verify_account&username=${encodeURIComponent(loginForm.username.trim())}&password=${encodeURIComponent(loginForm.password)}&account_jwt=${encodeURIComponent(props.accountToken)}`
    });
    const result = await response.json();
    if (result.status === 'need_mfa') {
      emit('need-mfa', {
        mfa_jwt: result.mfa_jwt,
        username: loginForm.username.trim()
      });
    } else if (result.status === 'ok') {
      saveLoginTokens(result);
      emit('login-success');
    } else {
      ElMessage.error('登录失败，请检查用户名和密码');
    }
  } catch (error) {
    ElMessage.error(error.message || '登录失败');
  } finally {
    loading.value = false;
  }
};

</script>

<style scoped>
@import './login-common.css';

/* 特定于表单的样式 */
.form-wrapper {
  width: 100%;
}

/* 表单样式重写 */
.common-form :deep(.el-input__wrapper) {
  background-color: rgba(255, 255, 255, 0.1);
  border: 1px solid rgba(255, 255, 255, 0.2);
  border-radius: 10px;
  backdrop-filter: blur(10px);
}

.common-form :deep(.el-input__wrapper:hover) {
  border-color: rgba(64, 158, 255, 0.5);
}

.common-form :deep(.el-input__wrapper.is-focus) {
  border-color: #409eff;
  box-shadow: 0 0 0 2px rgba(64, 158, 255, 0.2);
}

.common-form :deep(.el-input__inner) {
  color: white;
  background-color: transparent;
}

.common-form :deep(.el-input__inner::placeholder) {
  color: rgba(255, 255, 255, 0.6);
}

.common-form :deep(.el-input__prefix),
.common-form :deep(.el-input__suffix) {
  color: rgba(255, 255, 255, 0.6);
}

/* 字符计数样式 */
.common-form :deep(.el-input__count) {
  color: rgba(255, 255, 255, 0.6);
  background-color: transparent;
}

/* 错误状态样式 */
.common-form :deep(.el-form-item.is-error .el-input__wrapper) {
  border-color: #f56c6c;
  box-shadow: 0 0 0 2px rgba(245, 108, 108, 0.2);
}

.common-form :deep(.el-form-item.is-error .el-form-item__error) {
  display: none !important;
}
</style>
