<template>
  <div class="login-view">
    <!-- 使用背景组件 -->
    <BackGround />
    
    <!-- 登录容器 -->
    <div class="login-container">
      <!-- 系统logo和标题 -->

      <div class="login-header">
        <h1>{{ siteName }}</h1>
        <p class="subtitle">安全、快速的文件传输平台</p>
      </div>
      
      <!-- 初始化步骤 -->
      <div v-if="currentStep === 'init'" class="login-form-container">
        <div class="form-title">
          <i class="fas fa-tools"></i>
          <span>系统初始化</span>
        </div>
        <LoginInit @init-success="handleInitSuccess" />
      </div>
      
      <!-- 人机验证步骤 -->
      <div v-else-if="currentStep === 'verification'" class="login-form-container">
        <div class="form-title">
          <i class="fas fa-shield-alt"></i>
          <span>安全验证</span>
        </div>
        
        <div class="slider-container">
          <LoginSlider
            ref="sliderRef"
            @verified="handleSliderVerified"
            @failed="handleSliderFailed"
          />
        </div>
        <div class="verification-description">
          <p>为了保护系统安全，请完成人机验证</p>
        </div>
      </div>
      
      <!-- 登录表单步骤 -->
      <div v-else-if="currentStep === 'login'" class="login-form-container">
        <LoginForm 
          :account-token="accountToken"
          @login-success="handleLoginSuccess"
          @need-mfa="handleNeedMFA"
          ref="loginFormRef"
        />
      </div>
      
      <!-- MFA验证步骤 -->
      <div v-else-if="currentStep === 'mfa'" class="login-form-container">
        <LoginMfa
          :mfa-jwt="mfaToken"
          @login-success="handleLoginSuccess"
          @back-to-login="handleBackToLogin"
          ref="mfaFormRef"
        />
      </div>
      
      <!-- 状态信息 -->
      <div class="status-indicator">
        <div class="status-item">
          <i class="fas fa-circle"></i>
          <span>{{ currentStep === 'init' ? '等待初始化' : '安全登录中' }}</span>
        </div>
      </div>

      <!-- 协议提示 -->
      <div class="agreement-tip">
        <p>登录/初始化即代表同意
          <a :href="agreementUrl" target="_blank" class="agreement-link">《软件使用协议》</a>
        </p>
      </div>
    </div>
  </div>
  <FootMenu />
</template>

<script setup>
// 协议页面跳转链接，自动适配 http/https
const agreementUrl = `${window.location.protocol}//${window.location.host}/api/pact.php`;
import { ref, onMounted } from 'vue';
import { useRouter } from 'vue-router';
import { ElMessage } from 'element-plus';
import { ensureVisitJwt } from '../utils/auth';
import BackGround from '../components/Back-Ground.vue';
import LoginSlider from './login/Login-Slider.vue';
import LoginInit from './login/Login-Init.vue';
import LoginForm from './login/Login-Form.vue';
import LoginMfa from './login/Login-Mfa.vue';
import FootMenu from '../components/Foot-Menu.vue';

const router = useRouter();

// 站点名称
const siteName = ref('YKC云阶快传');

// 动态加载 site.json
const loadSiteName = async () => {
  try {
    const protocol = window.location.protocol;
    const host = window.location.host;
    const url = `${protocol}//${host}/api/data/site.json`;
    const resp = await fetch(url);
    if (resp.ok) {
      const data = await resp.json();
      if (data.siteName) siteName.value = data.siteName;
    }
  } catch (e) {
    // 忽略异常，使用默认
  }
};

// 响应式数据
const currentStep = ref('init'); // 'init' | 'verification' | 'login' | 'mfa'
const accountToken = ref('');
const mfaToken = ref('');

// 表单引用
const sliderRef = ref();
const loginFormRef = ref();
const mfaFormRef = ref();

// 检查系统是否初始化
const checkSystemInit = async () => {
  try {
    const params = new URLSearchParams();
    params.append('action', 'check_init');
    const response = await fetch('/api/auth/login.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: params.toString()
    });
    const result = await response.json();
    if (result.status === 'initialized') {
      // 已初始化，进入人机验证
      currentStep.value = 'verification';
    } else if (result.status === 'not_initialized') {
      // 未初始化，进入初始化流程
      currentStep.value = 'init';
    } else if (result.status === 'already_logged_in') {
      ElMessage.success('检测到已登录状态，正在跳转...');
      router.push('/file');
    } else if (result.status === 'ip_banned') {
      ElMessage.error(`IP已被封禁，解封时间: ${new Date(result.until * 1000).toLocaleString()}`);
    } else {
      // 其他未知状态，默认进入初始化
      currentStep.value = 'init';
    }
  } catch (error) {
    console.error('检查系统初始化状态失败:', error);
    ElMessage.error('系统检查失败，请刷新页面重试');
    currentStep.value = 'init';
  }
};

// 处理滑块验证成功
const handleSliderVerified = (accountTokenValue) => {
  if (!accountTokenValue) {
    ElMessage.error('验证成功但令牌无效，请重试');
    return;
  }
  
  accountToken.value = accountTokenValue;
  currentStep.value = 'login';
  ElMessage.success('人机验证成功，请登录');
};

// 处理滑块验证失败
const handleSliderFailed = () => {
  accountToken.value = '';
  ElMessage.error('人机验证失败，请重试');
  if (sliderRef.value) {
    sliderRef.value.reset();
  }
};

// 处理登录成功
const handleLoginSuccess = () => {
  ElMessage.success('登录成功，正在跳转...');
  setTimeout(() => {
    router.push('/file');
  }, 1000);
};

// 处理需要MFA
const handleNeedMFA = (mfaData) => {
  mfaToken.value = mfaData.mfa_jwt;
  currentStep.value = 'mfa';
  ElMessage.info('需要双重认证验证');
};

// 处理返回登录
const handleBackToLogin = () => {
  currentStep.value = 'verification';
  accountToken.value = '';
  mfaToken.value = '';
  if (sliderRef.value) {
    sliderRef.value.reset();
  }
};

// 初始化成功后自动刷新页面或重新检测系统状态
const handleInitSuccess = () => {
  // 推荐重新检测系统状态，体验更好
  checkSystemInit();
};

// 组件挂载时检查系统初始化状态和加载站点名
onMounted(async () => {
  // 首先检查用户是否已经登录
  const isLoggedIn = await ensureVisitJwt();
  if (isLoggedIn) {
    ElMessage.success('检测到登录状态，正在跳转...');
    router.push('/file');
    return;
  }
  
  // 未登录，继续正常的初始化检查流程
  checkSystemInit();
  loadSiteName();
});
</script>

