<template>
  <div class="mfa-form-wrapper">
    <div class="form-title">
      <i class="fas fa-shield-alt"></i>
      <span>双重认证</span>
    </div>
    
    <div class="mfa-info">
      <p>请输入身份验证器应用生成的6位验证码</p>
    </div>
    
    <div class="mfa-form">
      <div class="verification-input-container">
        <input
          v-model="verificationCode"
          type="text"
          class="verification-input"
          placeholder="000000"
          maxlength="6"
          @input="handleCodeInput"
          @keyup.enter="handleVerify"
          autofocus
        />
        <div v-if="error" class="error-message">
          <i class="fas fa-exclamation-triangle"></i>
          <span>{{ error }}</span>
        </div>
      </div>
      
      <button
        class="verify-btn"
        :disabled="verificationCode.length !== 6 || loading"
        @click="handleVerify"
      >
        <i v-if="loading" class="fas fa-spinner fa-spin"></i>
        <i v-else class="fas fa-check"></i>
        {{ loading ? '验证中...' : '验证' }}
      </button>
    </div>
    
    <div class="mfa-tips">
      <div class="tip-item">
        <i class="fas fa-info-circle"></i>
        <span>验证码每30秒更新一次</span>
      </div>
      <div class="tip-item">
        <i class="fas fa-mobile-alt"></i>
        <span>请使用您设置的身份验证器应用</span>
      </div>
    </div>
    
    <div class="back-action">
      <button class="back-btn" @click="handleBack">
        <i class="fas fa-arrow-left"></i>
        返回登录
      </button>
    </div>
  </div>
</template>

<script setup>
import { ref, defineProps, defineEmits } from 'vue';
import { ElMessage } from 'element-plus';
import { saveLoginTokens } from '../../utils/auth';

// Props
const props = defineProps({
  mfaJwt: {
    type: String,
    required: true
  }
});

// Emits
const emit = defineEmits(['login-success', 'back-to-login']);

// 响应式数据
const loading = ref(false);
const verificationCode = ref('');
const error = ref('');

// 处理验证码输入
const handleCodeInput = () => {
  error.value = '';
  // 只允许数字
  verificationCode.value = verificationCode.value.replace(/\D/g, '');
};

// 处理MFA验证
const handleVerify = async () => {
  if (verificationCode.value.length !== 6) {
    error.value = '请输入6位验证码';
    return;
  }
  
  loading.value = true;
  error.value = '';
  
  try {
    const response = await fetch('/api/auth/login.php', {
      method: 'POST',
      headers: {
        'Content-Type': 'application/x-www-form-urlencoded'
      },
      body: `action=verify_mfa&mfa_jwt=${encodeURIComponent(props.mfaJwt)}&mfa_token=${encodeURIComponent(verificationCode.value)}`
    });
    const result = await response.json();
    if (result.status === 'ok') {
      saveLoginTokens(result);
      ElMessage.success('验证成功');
      emit('login-success');
    } else {
      error.value = '验证码错误';
      verificationCode.value = '';
    }
  } catch (error) {
    ElMessage.error(error.message || '验证失败');
    if (error.message && error.message.includes('令牌')) {
      setTimeout(() => {
        emit('back-to-login');
      }, 2000);
    } else {
      error.value = error.message || '验证失败';
      verificationCode.value = '';
    }
  } finally {
    loading.value = false;
  }
};

// 返回登录
const handleBack = () => {
  emit('back-to-login');
};
</script>

